/* required styles */

.leaflet-pane,
.leaflet-tile,
.leaflet-marker-icon,
.leaflet-marker-shadow,
.leaflet-tile-container {
    position: absolute;
    inset-inline-start: 0;
    top: 0;
}

.leaflet-pane > {
    svg,
    canvas {
        position: absolute;
        inset-inline-start: 0;
        top: 0;
    }
}

.leaflet-zoom-box,
.leaflet-image-layer,
.leaflet-layer {
    position: absolute;
    inset-inline-start: 0;
    top: 0;
}

.leaflet-container {
    overflow: hidden;
}

.leaflet-tile,
.leaflet-marker-icon,
.leaflet-marker-shadow {
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    -webkit-user-drag: none;
}

/* Safari renders non-retina tile on retina better with this, but Chrome is worse */

.leaflet-safari {
    .leaflet-tile {
        image-rendering: -webkit-optimize-contrast;
    }

    .leaflet-tile-container {
        width: 1600px;
        height: 1600px;
        -webkit-transform-origin: 0 0;
        transform-origin: 0 0;
    }
}

/* hack that prevents hw layers "stretching" when loading new tiles */

.leaflet-marker-icon,
.leaflet-marker-shadow {
    display: block;
}

/* .leaflet-container svg: reset svg max-width decleration shipped in Joomla! (joomla.org) 3.x */
/* .leaflet-container img: map is broken in FF if you have max-width: 100% on tiles */

.leaflet-container {
    .leaflet-overlay-pane svg,
    .leaflet-marker-pane img,
    .leaflet-shadow-pane img,
    .leaflet-tile-pane img,
    img.leaflet-image-layer,
    .leaflet-tile {
        max-width: none !important;
        max-height: none !important;
    }

    &.leaflet-touch-zoom {
        -ms-touch-action: pan-x pan-y;
        touch-action: pan-x pan-y;
    }

    &.leaflet-touch-drag {
        -ms-touch-action: pinch-zoom;

        /* Fallback for FF which doesn't support pinch-zoom */
        touch-action: none;
        touch-action: pinch-zoom;

        &.leaflet-touch-zoom {
            -ms-touch-action: none;
            touch-action: none;
        }
    }

    -webkit-tap-highlight-color: transparent;

    a {
        -webkit-tap-highlight-color: rgba(51, 181, 229, 0.4);
    }
}

.leaflet-tile {
    filter: inherit;
    visibility: hidden;
}

.leaflet-tile-loaded {
    visibility: inherit;
}

.leaflet-zoom-box {
    width: 0;
    height: 0;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    z-index: 800;
}

/* workaround for https://bugzilla.mozilla.org/show_bug.cgi?id=888319 */

.leaflet-overlay-pane svg {
    -moz-user-select: none;
    user-select: none;
}

.leaflet-pane {
    z-index: 1;
}

.leaflet-tile-pane {
    z-index: 200;
}

.leaflet-overlay-pane {
    z-index: 400;
}

.leaflet-shadow-pane {
    z-index: 500;
}

.leaflet-marker-pane {
    z-index: 600;
}

.leaflet-tooltip-pane {
    z-index: 650;
}

.leaflet-popup-pane {
    z-index: 700;
}

.leaflet-map-pane {
    canvas {
        z-index: 100;
    }

    svg {
        z-index: 200;
    }
}

.leaflet-vml-shape {
    width: 1px;
    height: 1px;
}

.lvml {
    behavior: url(#default#VML);
    display: inline-block;
    position: absolute;
}

/* control positioning */

.leaflet-control {
    position: relative;
    z-index: 1;
    pointer-events: visiblePainted;

    /* IE 9-10 doesn't have auto */
    pointer-events: auto;
}

.leaflet-top,
.leaflet-bottom {
    position: absolute;
    z-index: 1;
    pointer-events: none;
}

.leaflet-top {
    top: 0;
}

.leaflet-right {
    inset-inline-end: 0;
}

.leaflet-bottom {
    bottom: 0;
}

.leaflet-left {
    inset-inline-start: 0;
}

.leaflet-control {
    float: $float-left;
    clear: both;
}

.leaflet-right .leaflet-control {
    float: $float-right;
}

.leaflet-top .leaflet-control {
    margin-block-start: 10px;
}

.leaflet-bottom .leaflet-control {
    margin-block-end: 10px;
}

.leaflet-left .leaflet-control {
    margin-inline-start: 10px;
}

.leaflet-right .leaflet-control {
    margin-inline-end: 10px;
}

/* zoom and fade animations */

.leaflet-fade-anim {
    .leaflet-tile {
        will-change: opacity;
    }

    .leaflet-popup {
        opacity: 0;
        -webkit-transition: opacity 0.2s linear;
        -moz-transition: opacity 0.2s linear;
        transition: opacity 0.2s linear;
    }

    .leaflet-map-pane .leaflet-popup {
        opacity: 1;
    }
}

.leaflet-zoom-animated {
    -webkit-transform-origin: 0 0;
    -ms-transform-origin: 0 0;
    transform-origin: 0 0;
}

.leaflet-zoom-anim {
    .leaflet-zoom-animated {
        will-change: transform;
        -webkit-transition: -webkit-transform 0.25s cubic-bezier(0, 0, 0.25, 1);
        -moz-transition: -moz-transform 0.25s cubic-bezier(0, 0, 0.25, 1);
        transition: transform 0.25s cubic-bezier(0, 0, 0.25, 1);
    }

    .leaflet-tile {
        -webkit-transition: none;
        -moz-transition: none;
        transition: none;
    }
}

.leaflet-pan-anim .leaflet-tile {
    -webkit-transition: none;
    -moz-transition: none;
    transition: none;
}

.leaflet-zoom-anim .leaflet-zoom-hide {
    visibility: hidden;
}

/* cursors */

.leaflet-interactive {
    cursor: pointer;
}

.leaflet-grab {
    cursor: -webkit-grab;
    cursor: -moz-grab;
    cursor: grab;
}

.leaflet-crosshair {
    cursor: crosshair;

    .leaflet-interactive {
        cursor: crosshair;
    }
}

.leaflet-popup-pane,
.leaflet-control {
    cursor: auto;
}

.leaflet-dragging {
    .leaflet-grab {
        cursor: move;
        cursor: -webkit-grabbing;
        cursor: -moz-grabbing;
        cursor: grabbing;

        .leaflet-interactive {
            cursor: move;
            cursor: -webkit-grabbing;
            cursor: -moz-grabbing;
            cursor: grabbing;
        }
    }

    .leaflet-marker-draggable {
        cursor: move;
        cursor: -webkit-grabbing;
        cursor: -moz-grabbing;
        cursor: grabbing;
    }
}

/* marker & overlays interactivity */

.leaflet-marker-icon,
.leaflet-marker-shadow,
.leaflet-image-layer,
.leaflet-pane > svg path,
.leaflet-tile-container {
    pointer-events: none;
}

.leaflet-marker-icon.leaflet-interactive,
.leaflet-image-layer.leaflet-interactive,
.leaflet-pane > svg path.leaflet-interactive {
    pointer-events: visiblePainted;

    /* IE 9-10 doesn't have auto */
    pointer-events: auto;
}

/* visual tweaks */

.leaflet-container {
    background: #ddd;
    outline: 0;

    a {
        color: #0078a8;

        &.leaflet-active {
            outline: 2px solid orange;
        }
    }
}

.leaflet-zoom-box {
    border: 2px dotted #38f;
    background: $white-5;
}

/* general typography */

.leaflet-container {
    font: 12px/1.5 "Helvetica Neue", Arial, Helvetica, sans-serif;
}

/* general toolbar styles */

.leaflet-bar {
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.65);
    border-radius: 4px;

    a {
        background-color: $white;
        border-block-end: 1px solid #ccc;
        width: 26px;
        height: 26px;
        line-height: 26px;
        display: block;
        text-align: center;
        text-decoration: none;
        color: #000 !important;

        &:hover {
            background-color: $white;
            border-block-end: 1px solid #ccc;
            width: 26px;
            height: 26px;
            line-height: 26px;
            display: block;
            text-align: center;
            text-decoration: none;
            color: black;
        }

        background-position: 50% 50%;
        background-repeat: no-repeat;
        display: block;
    }
}

.leaflet-control-layers-toggle {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    display: block;
}

.leaflet-bar a {
    &:hover {
        background-color: #f4f4f4;
    }

    &:first-child {
        border-start-start-radius: 4px;
        border-start-end-radius: 4px;
    }

    &:last-child {
        border-end-start-radius: 4px;
        border-end-end-radius: 4px;
        border-block-end: none;
    }

    &.leaflet-disabled {
        cursor: default;
        background-color: #f4f4f4;
        color: #bbb;
    }
}

.leaflet-touch .leaflet-bar a {
    width: 30px;
    height: 30px;
    line-height: 30px;

    &:first-child {
        border-start-start-radius: 2px;
        border-start-end-radius: 2px;
    }

    &:last-child {
        border-end-start-radius: 2px;
        border-end-end-radius: 2px;
    }
}

/* zoom control */

.leaflet-control-zoom-in,
.leaflet-control-zoom-out {
    font: bold 18px "Lucida Console", Monaco, monospace;
    text-indent: 1px;
}

.leaflet-touch {
    .leaflet-control-zoom-in,
    .leaflet-control-zoom-out {
        font-size: 22px;
    }
}

/* layers control */

.leaflet-control-layers {
    box-shadow: 0 1px 5px $black-4;
    background: $white;
    border-radius: 5px;
}

.leaflet-control-layers-toggle {
    background-image: url(../plugins/leaflet/images/layers.png);
    width: 36px;
    height: 36px;
}

.leaflet-retina .leaflet-control-layers-toggle {
    background-image: url(../plugins/leaflet/images/layers-2x.png);
    background-size: 26px 26px;
}

.leaflet-touch .leaflet-control-layers-toggle {
    width: 44px;
    height: 44px;
}

.leaflet-control-layers .leaflet-control-layers-list {
    display: none;
}

.leaflet-control-layers-expanded {
    .leaflet-control-layers-toggle {
        display: none;
    }

    .leaflet-control-layers-list {
        display: block;
        position: relative;
    }

    padding: 6px 10px 6px 6px;
    color: #333;
    background: $white;
}

.leaflet-control-layers-scrollbar {
    overflow-y: scroll;
    overflow-x: hidden;
    padding-inline-end: 5px;
}

.leaflet-control-layers-selector {
    margin-block-start: 2px;
    position: relative;
    top: 1px;
}

.leaflet-control-layers label {
    display: block;
}

.leaflet-control-layers-separator {
    height: 0;
    border-block-start: 1px solid #ddd;
    margin: 5px -10px 5px -6px;
}

/* Default icon URLs */

.leaflet-default-icon-path {
    background-image: url(../plugins/leaflet/images/marker-icon.png);
}

/* attribution and scale controls */

.leaflet-container .leaflet-control-attribution {
    background: $white;
    background: $white-7;
    margin: 0;
}

.leaflet-control-attribution,
.leaflet-control-scale-line {
    padding: 0 5px;
    color: #333;
}

.leaflet-control-attribution a {
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.leaflet-container {
    .leaflet-control-attribution,
    .leaflet-control-scale {
        font-size: 11px;
    }
}

.leaflet-left .leaflet-control-scale {
    margin-inline-start: 5px;
}

.leaflet-bottom .leaflet-control-scale {
    margin-block-end: 5px;
}

.leaflet-control-scale-line {
    border: 2px solid #777;
    border-block-start: none;
    line-height: 1.1;
    padding: 2px 5px 1px;
    font-size: 11px;
    white-space: nowrap;
    overflow: hidden;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: $white;
    background: $white-5;

    &:not(:first-child) {
        border-block-start: 2px solid #777;
        border-block-end: none;
        margin-block-start: -2px;

        &:not(:last-child) {
            border-block-end: 2px solid #777;
        }
    }
}

.leaflet-touch {
    .leaflet-control-attribution,
    .leaflet-control-layers,
    .leaflet-bar {
        box-shadow: none;
    }

    .leaflet-control-layers,
    .leaflet-bar {
        border: 2px solid $black-2;
        background-clip: padding-box;
    }
}

/* popup */

.leaflet-popup {
    position: absolute;
    text-align: center;
    margin-block-end: 20px;
}

.leaflet-popup-content-wrapper {
    padding: 1px;
    text-align: start;
    border-radius: 12px;
}

.leaflet-popup-content {
    margin: 13px 19px;
    line-height: 1.4;

    p {
        margin: 18px 0;
    }
}

.leaflet-popup-tip-container {
    width: 40px;
    height: 20px;
    position: absolute;
    inset-inline-start: 50%;
    margin-inline-start: -20px;
    overflow: hidden;
    pointer-events: none;
}

.leaflet-popup-tip {
    width: 17px;
    height: 17px;
    padding: 1px;
    margin: -10px auto 0;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.leaflet-popup-content-wrapper,
.leaflet-popup-tip {
    background: white;
    color: #333;
    box-shadow: 0 3px 14px $black-4;
}

.leaflet-container a.leaflet-popup-close-button {
    position: absolute;
    top: 0;
    inset-inline-end: 0;
    padding: 4px 4px 0 0;
    border: none;
    text-align: center;
    width: 18px;
    height: 14px;
    font: 16px/14px Tahoma, Verdana, sans-serif;
    color: #c3c3c3;
    text-decoration: none;
    font-weight: bold;
    background: transparent;

    &:hover {
        color: #999;
    }
}

.leaflet-popup-scrolled {
    overflow: auto;
    border-block-end: 1px solid #ddd;
    border-block-start: 1px solid #ddd;
}

.leaflet-oldie {
    .leaflet-popup-content-wrapper {
        zoom: 1;
    }

    .leaflet-popup-tip {
        width: 24px;
        margin: 0 auto;
        -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0.70710678, M12=0.70710678, M21=-0.70710678, M22=0.70710678)";
        filter: progid:DXImageTransform.Microsoft.Matrix(M11=0.70710678, M12=0.70710678, M21=-0.70710678, M22=0.70710678);
    }

    .leaflet-popup-tip-container {
        margin-block-start: -1px;
    }

    .leaflet-control-zoom,
    .leaflet-control-layers,
    .leaflet-popup-content-wrapper,
    .leaflet-popup-tip {
        border: 1px solid #999;
    }
}

/* div icon */

.leaflet-div-icon {
    background: $white;
    border: 1px solid #666;
}

/* Tooltip */
/* Base styles for the element that has a tooltip */

.leaflet-tooltip {
    position: absolute;
    padding: 6px;
    background-color: $white;
    border: 1px solid $white;
    border-radius: 3px;
    color: #222;
    white-space: nowrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    pointer-events: none;
    box-shadow: 0 1px 3px $black-4;

    &.leaflet-clickable {
        cursor: pointer;
        pointer-events: auto;
    }
}

.leaflet-tooltip-top:before,
.leaflet-tooltip-bottom:before,
.leaflet-tooltip-inset-inline-start:before,
.leaflet-tooltip-inset-inline-end:before {
    position: absolute;
    pointer-events: none;
    border: 6px solid transparent;
    background: transparent;
    content: "";
}

/* Directions */

.leaflet-tooltip-bottom {
    margin-block-start: 6px;
}

.leaflet-tooltip-top {
    margin-block-start: -6px;
}

.leaflet-tooltip-bottom:before {
    inset-inline-start: 50%;
    margin-inline-start: -6px;
}

.leaflet-tooltip-top:before {
    inset-inline-start: 50%;
    margin-inline-start: -6px;
    bottom: 0;
    margin-block-end: -12px;
    border-block-start-color: $white;
}

.leaflet-tooltip-bottom:before {
    top: 0;
    margin-block-start: -12px;
    margin-inline-start: -6px;
    border-block-end-color: $white;
}

.leaflet-tooltip-left {
    margin-inline-start: -6px;
}

.leaflet-tooltip-right {
    margin-inline-start: 6px;
}

.leaflet-tooltip-inset-inline-start:before,
.leaflet-tooltip-inset-inline-end:before {
    top: 50%;
    margin-block-start: -6px;
}

.leaflet-tooltip-inset-inline-start:before {
    inset-inline-end: 0;
    margin-inline-end: -12px;
   border-inline-start-color: $white;
}

.leaflet-tooltip-inset-inline-end:before {
    inset-inline-start: 0;
    margin-inline-start: -12px;
    border-inline-end-color: $white;
}
